*{
	margin:0;
}
.App {
  width:90%;
  margin:auto;
  box-sizing: border-box;
}
.title {
	margin:50px 0;
	font-weight:500;
}
.m5 {
	margin:10px 0;
	font-weight: 500;
}
.m20 {
	margin:20px 0;
}

.grow {
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform .25s ease-out;
}
.grow:hover{
    transform: scale(1.02);
}

@media (max-width:600px) {
  .App{
  	width:95%;
    font-size:.7rem;
  }
  .title{
  	margin:20px 0;
  }
}
